.buttons_header{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.button_header{
  margin: 0px 10px 0px 10px;
  width: 130px;
}

.active_button{
  background-color: var(--color_principal);
}
.active_button:hover{
  background-color: var(--color_principal_claro);
  
}

@media(max-width: 768px) {
  .buttons_header {
    width: 100%;
    flex-wrap: wrap;
    height: auto;
  }
  .button_header{
    margin: 0px 10px 10px 10px;
  }
} 