.searchBox {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  flex-direction: column;
}
.search-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  padding: 0px 0px 0px 20px;
}
.search-input-input {
  width: 90%;
}
.searchBox small {
  margin-top: 10px;
}

@media (max-width: 768px) {
  .search-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    padding: 0px 0px 0px 20px;
  }
}
