@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700;800&display=swap);

.header_pages {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  height: 150px;
}

.header_pages h1 {
  text-transform: uppercase;
  font-size: 40px;
  font-weight: 300;
  font-family: "Rubik", sans-serif;
  padding-bottom: 5px;
  border-bottom: 1px solid black;
}

.buttons_header{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.button_header{
  margin: 0px 10px 0px 10px;
  width: 130px;
}

.active_button{
  background-color: var(--color_principal);
}
.active_button:hover{
  background-color: var(--color_principal_claro);
  
}

@media(max-width: 768px) {
  .buttons_header {
    width: 100%;
    flex-wrap: wrap;
    height: auto;
  }
  .button_header{
    margin: 0px 10px 10px 10px;
  }
} 
.cheat_list{
  display: flex;
  width: 60%;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 970px;
}
.cheats_zone{
  width: 100%;
  display: flex;
  justify-content: center;
}
.pagination_div{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
