.cheat_box {
  display: block;
  width: 270px;
  height: 334px;
  border: 1px solid #dcdcdc;
  border-radius: 16px;
  background: #fff;
  margin: 10px;
  overflow: hidden;
  cursor: pointer;
  flex-shrink: 0;
  position: relative;
  right: 0;
  transition: right 0.3s;
}

.cheat_box:hover .cheat_box_header > .headerIMG {
  -webkit-transform: scale(1.2) rotate(1deg);
          transform: scale(1.2) rotate(1deg);
}

.cheat_box_header {
  height: 133px;
  overflow: hidden;
  position: relative;
}

.cheat_box_header .headerIMG {
  width: 100%;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  transition: all 1s;
}

.cheat_box_header .icon {
  background-color: rgb(0, 0, 0);
  border-radius: 5px;
  padding: 2px;
  width: 25px;
  position: absolute;
  top: 5px;
  right: 10px;
  z-index: 2;
  text-shadow: 0 3px 5px 1px rgba(51, 51, 51, 0.3);
  opacity: 0.8;
}

.admin-options {
  display: flex;
}

.iconOptions {
  border-radius: 5px;
  padding: 2px;
  width: 30px;
  height: 30px;
  z-index: 2;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}

.iconEdit {
  background-color: rgb(255, 145, 0);
  color: black;
  margin-left: 5px;
}
.iconEdit:hover {
  background-color: rgb(255, 186, 95);
}
.iconTrash {
  background-color: rgb(0, 0, 0);
  color: white;
}
.iconTrash:hover {
  background-color: var(--color_principal);
}

.buttons {
  display: flex;
}

.cheat_box_details {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 25px;
  padding-top: 10px;
  color: #404040;
  box-sizing: border-box;
}

.cheat_box_details::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 25px;
  width: 100%;
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 28%,
    rgba(255, 255, 255, 0.9) 40%,
    rgba(255, 255, 255, 0.1) 90%,
    rgba(255, 255, 255, 0) 100%
  );
}
.cheat_box_body .icono {
  color: #404040;
  font-size: 12px;
  margin-right: 7px;
}
.cheat_box_body .fecha {
  top: 10px;
  right: 20px;
  color: #404040;
  display: flex;
  align-items: center;
  font-size: 11px;
  font-weight: 500;
  position: absolute;
  width: 100%;
  justify-content: flex-end;
}
.cheat_box_body {
  position: relative;
  height: calc(100% - 133px);
  padding: 14px 25px 18px;
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: space-around;
}
.cheat_box_body .title {
  display: flex;
  align-items: center;
  font-size: 15px;
  font-weight: 700;
  color: #1e1e1e;
  margin-top: 10px;
}

.cheat_box_body .title span {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.cheat_box_body .title img {
  width: 20px;
  margin-right: 5px;
  margin-left: -10px;
}

.cheat_box_body .description {
  position: relative;
  font-size: 14px;
  color: #787878;
  line-height: 1.5;
  height: 63px;
  margin-bottom: 12px;
  overflow: hidden;
}

.download_button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 130px;
  margin: 0 auto;
  font-size: 12.1px;
  color: #000;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  /* background-color: rgb(111, 255, 118); */
  transition: 0.5s;
  overflow: hidden;
}
.download_button:hover {
  /* background-color: rgba(111, 255, 118, 0.671); */
  -webkit-transform: scale(1.1) rotate(1deg);
          transform: scale(1.1) rotate(1deg);
}
.download_button i {
  font-size: 13px;
  margin-right: 8px;
}

.searchBox {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  flex-direction: column;
}
.search-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  padding: 0px 0px 0px 20px;
}
.search-input-input {
  width: 90%;
}
.searchBox small {
  margin-top: 10px;
}

@media (max-width: 768px) {
  .search-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    padding: 0px 0px 0px 20px;
  }
}

